console.log('Events.js loaded'); // ‚úÖ –î–ª—è –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏

// –ß–µ–∫–∞—î–º–æ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è DOM
document.addEventListener('DOMContentLoaded', () => {
  console.log('DOMContentLoaded fired in events.js');
  // ========== SLIDER LOGIC ==========
  const slider = document.querySelector('.events-list');
  const slides = document.querySelectorAll('.event-item');
  const prevBtn = document.querySelector('.slider-btn.prev');
  const nextBtn = document.querySelector('.slider-btn.next');
  const paginationDots = document.querySelectorAll('.pagination-dot');

  if (!slider || !slides.length) {
    console.error('Slider elements not found');
    return;
  }

  let currentIndex = 0;

  function getVisibleCount() {
    if (window.innerWidth >= 1440) return 3;
    if (window.innerWidth >= 768) return 2;
    return 1;
  }

  function updateSlider() {
    const visible = getVisibleCount();
    
        // –î–ª—è –º–æ–±—ñ–ª—å–Ω–æ—ó —Ç–∞ –ø–ª–∞–Ω—à–µ—Ç–∞
    const slideWidth = slides[0].offsetWidth;
    const gap = window.innerWidth >= 768 ? 24 : 24;
    const offset = currentIndex * (slideWidth + gap);
    
    slider.style.transform = `translateX(-${offset}px)`;
    
    // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –∫–Ω–æ–ø–æ–∫
    const maxIndex = Math.max(0, slides.length - visible);
    if (prevBtn) prevBtn.disabled = currentIndex === 0;
    if (nextBtn) nextBtn.disabled = currentIndex >= maxIndex;
    
    // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –ø–∞–≥—ñ–Ω–∞—Ü—ñ—ó
    paginationDots.forEach((dot, index) => {
      dot.classList.toggle('active', index === currentIndex);
    });
  }

  // –ö–Ω–æ–ø–∫–∏ –Ω–∞–≤—ñ–≥–∞—Ü—ñ—ó
  if (nextBtn) {
    nextBtn.addEventListener('click', () => {
      const visible = getVisibleCount();
      const maxIndex = Math.max(0, slides.length - visible);
      if (currentIndex < maxIndex) {
        currentIndex++;
        updateSlider();
      }
    });
  }

  if (prevBtn) {
    prevBtn.addEventListener('click', () => {
      if (currentIndex > 0) {
        currentIndex--;
        updateSlider();
      }
    });
  }

  // –ü–∞–≥—ñ–Ω–∞—Ü—ñ—è - –∫–ª—ñ–∫ –ø–æ —Ç–æ—á–∫–∞—Ö
  paginationDots.forEach((dot, index) => {
    dot.addEventListener('click', () => {
      const visible = getVisibleCount();
      const maxIndex = Math.max(0, slides.length - visible);
      currentIndex = Math.min(index, maxIndex);
      updateSlider();
    });
  });

  // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –ø—Ä–∏ –∑–º—ñ–Ω—ñ —Ä–æ–∑–º—ñ—Ä—É –µ–∫—Ä–∞–Ω–∞
  let resizeTimeout;
  window.addEventListener('resize', () => {
    clearTimeout(resizeTimeout);
    resizeTimeout = setTimeout(() => {
      const visible = getVisibleCount();
      const maxIndex = Math.max(0, slides.length - visible);
      if (currentIndex > maxIndex) {
        currentIndex = maxIndex;
      }
      updateSlider();
    }, 100);
  });

  // –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è
  updateSlider();

  // ========== MODAL LOGIC ==========
  const modal = document.getElementById('eventModal');
  const modalEventName = document.getElementById('modalEventName');
  const modalClose = document.querySelector('.modal-close');
  const eventForm = document.getElementById('eventForm');
  const eventButtons = document.querySelectorAll('.event-btn');

  if (!modal || !modalEventName || !modalClose || !eventForm) {
    console.error('Modal elements not found');
    return;
  }

  console.log('Found event buttons:', eventButtons.length);

  // –í—ñ–¥–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞
  eventButtons.forEach((btn, index) => {
    console.log(`Setting up button ${index}`, btn);
    btn.addEventListener('click', (e) => {
      e.preventDefault();
      const eventName = btn.getAttribute('data-event');
      console.log('Button clicked, event:', eventName);
      modalEventName.textContent = eventName;
      modal.classList.remove('is-hidden');
      document.body.style.overflow = 'hidden';
    });
  });

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞
  function closeModal() {
    modal.classList.add('is-hidden');
    document.body.style.overflow = '';
    eventForm.reset();
  }

  modalClose.addEventListener('click', (e) => {
    e.preventDefault();
    closeModal();
  });

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–ª—ñ–∫—É –Ω–∞ backdrop
  modal.addEventListener('click', (e) => {
    if (e.target === modal) {
      closeModal();
    }
  });

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ Escape
  document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape' && !modal.classList.contains('is-hidden')) {
      closeModal();
    }
  });

  // –í—ñ–¥–ø—Ä–∞–≤–∫–∞ —Ñ–æ—Ä–º–∏
  eventForm.addEventListener('submit', (e) => {
    e.preventDefault();
    
    const formData = new FormData(eventForm);
    const data = Object.fromEntries(formData);
    
    console.log('Form submitted:', data);
    
    // –ü–æ–∫–∞–∑–∞—Ç–∏ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    alert(`Thank you for registering for "${modalEventName.textContent}"! We'll contact you soon.`);
    
    closeModal();
  });
});
